<template>
  <a-config-provider :locale="zh_CN">
    <a-spin :tip="tip" :spinning="loading" :delay="delay" :size="size">
      <router-view />
    </a-spin>
  </a-config-provider>
</template>

<script lang="ts">
import { defineComponent, onMounted, reactive, toRefs, watch } from 'vue';
import zh_CN from 'ant-design-vue/es/locale/zh_CN';
import { useRoute, RouteMeta } from 'vue-router';
import { message } from 'ant-design-vue';
import appModule from '@/store/modules/app';

/**
 * @description: 
 * @return {void}
 */
function detectionBrowser() {
  const browserName = navigator.userAgent.toLowerCase();
  if (/msie/i.test(browserName) && !/opera/.test(browserName)) {
    message.warning({
      content: ` 360`,
      title: '',
    });
    return;
  } else if (/firefox/i.test(browserName)) {
    return;
  } else if (/chrome/i.test(browserName) && /webkit/i.test(browserName) && /mozilla/i.test(browserName)) {
    return;
  } else {
    message.warning({
      content: ` 360`,
      title: '',
    });
  }
}

export default defineComponent({
  setup() {
    onMounted(() => {
      detectionBrowser();
    });

    const { tip, delay, loading, size } = appModule.fullLoadingOpt;
    const state = reactive({
      tip,
      delay,
      loading,
      size,
    });

    watch(
      () => appModule.fullLoadingOpt,
      (v: any) => {
        Object.assign(state, v);
      },
      { deep: true, immediate: true }
    );

    const APP_TITLE = process.env.VUE_APP_TITLE;
    const route = useRoute();
    watch(
      () => route.meta,
      (value: RouteMeta) => {
        if (value && value.title) {
          document.title = value.title + ' - ' + APP_TITLE;
        } else {
          document.title = APP_TITLE!;
        }
      }
    );

    return {
      zh_CN,
      ...toRefs(state),
    };
  },
});
</script>

<style lang="less">
@import url('./app.less');
</style>
