
import { defineComponent } from 'vue';
import Breadcrumb from './Breadcrumb.vue';
/**  */
export default defineComponent({
  name: 'PageHeader',

  components: { Breadcrumb },

  data() {
    return {
      title: '',
    };
  },

  watch: {
    $route(val: any) {
      if (val.meta) {
        this.title = val.meta.title;
      }
    },
  },
});
